<template>
    <v-container fluid fill-height style="padding:0;" id="pixels" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. <code>ImageData</code>专门用来存储canvas一块儿区域的像素值</p>
                                    <table>
                                        <tr>
                                            <td> </td>
                                            <th>属性名</th>
                                            <th>属性值例子</th>
                                            <th>解释</th>
                                        </tr>
                                        <tr>
                                            <th>1</th>
                                            <td>width</td>
                                            <td>1</td>
                                            <td>表示像素块儿的宽度，也就是横向1个像素</td>
                                        </tr>
                                        <tr>
                                            <th>2</th>
                                            <td>height</td>
                                            <td>1</td>
                                            <td>表示像素块儿的高度，也就是纵向向1个像素</td>
                                        </tr>
                                        <tr>
                                            <th>3</th>
                                            <td>data</td>
                                            <td>[218, 252, 100, 255]</td>
                                            <td>代表rgba四个值，所有值的范围都是0~255</td>
                                        </tr>
                                    </table>
                                    <p class=".body-1">2. <code>createImageData(width, height)</code> 根据指定的像素块儿大小创建一个ImageData对象，所有的像素单位都被填充为0</p>
                                    <p class=".body-1">3. <code>putImageData(imageData, dx, dy)</code> 将ImageData对象绘制在canvas指定的位置中</p>
                                    <p class=".body-1">4. <code>getImageData(left, top, width, height)</code> 获取指定位置，指定区块儿的imageData对象</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <canvas id="canvas" width="400px" height="400px"></canvas>
                                    <canvas id="co-canvas" width="400px" height="400px"></canvas>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="js">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{js}}</pre>
                                            </v-card-text>
