        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from 'timers';
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            title: "利用ImageData处理大量像素渲染",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <canvas 
                id="canvas" 
                width="400px" 
                height="400px"
            >
            </canvas>
            <canvas 
                id="co-canvas" 
                width="400px" 
                height="400px"
            >
            </canvas>
            `,
            js: `
            const canvas = document.getElementById('canvas');
            const ctx = canvas.getContext('2d');
            const coCanvas = document.getElementById('co-canvas');
            const coCtx = coCanvas.getContext('2d');

            const img = ctx.createImageData(300, 300);
            for(let i = 0; i < img.data.length; i+=4) {
                img.data[i] = Math.floor(Math.random()*255); // red
                img.data[i+1] = Math.floor(Math.random()*255); // green
                img.data[i+2] = Math.floor(Math.random()*255); // blue
                img.data[i+3] = 255; // alpha
            }
            ctx.putImageData(img, 0, 0); // 渲染ImageData

            canvas.addEventListener('mousemove', function(e){
                // 获取鼠标位置
                const cRect = canvas.getBoundingClientRect();              
                const mouseX = Math.round(e.clientX - cRect.left);        
                const mouseY = Math.round(e.clientY - cRect.top);
                let px = ctx.getImageData(mouseX, mouseY, 1, 1).data;
                drawPixel('rgba(' + px[0] + ', ' + px[1] + ', ' + px[2] + ', ' + (px[3] / 255) + ')');
            })

            // 将pixel放入到co-canvas中
            function drawPixel(rgba) {
                coCtx.clearRect(0, 0, coCanvas.width, coCanvas.height);
                coCtx.fillStyle = rgba;
                coCtx.fillRect(0, 0, 300, 300);
            }
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        const coCanvas = document.getElementById('co-canvas');
        const coCtx = coCanvas.getContext('2d');

        const img = ctx.createImageData(300, 300);
        for(let i = 0; i < img.data.length; i+=4) {
            img.data[i] = Math.floor(Math.random()*255); // red
            img.data[i+1] = Math.floor(Math.random()*255); // green
            img.data[i+2] = Math.floor(Math.random()*255); // blue
            img.data[i+3] = 255; // alpha
        }
        ctx.putImageData(img, 0, 0); // 渲染ImageData

        canvas.addEventListener('mousemove', function(e){
            // 获取鼠标位置
            const cRect = canvas.getBoundingClientRect();              
            const mouseX = Math.round(e.clientX - cRect.left);        
            const mouseY = Math.round(e.clientY - cRect.top);
            let px = ctx.getImageData(mouseX, mouseY, 1, 1).data;
            drawPixel('rgba(' + px[0] + ', ' + px[1] + ', ' + px[2] + ', ' + (px[3] / 255) + ')');
        })

        // 将pixel放入到co-canvas中
        function drawPixel(rgba) {
            coCtx.clearRect(0, 0, coCanvas.width, coCanvas.height);
            coCtx.fillStyle = rgba;
            coCtx.fillRect(0, 0, 300, 300);
        }

    }
};
</script>

